body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: cadetblue;
}

.search-bar {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  
}

.search-bar input[type='text'] {
  padding: 10px;
  width: 730px;
  text-align: center;
  
  font-weight: bold;
}


.movie-list{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  grid-gap: 50px;
  margin-top: 20px;
  margin-left: 20px;
}

img{
  width: 200px;
}

.movie-title{
  font-family: cursive;
  font-size: 15px;
  text-align: center;
}